body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color:rgba(0,0,0,0.8);
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-image: url(/TomHodson/static/media/doiSuthepSummit.5d736824.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.7;
}

.bg{
  height: 100%;
}

.banner{
  width: 100%;
  align-content: right;
  display: flex;
  position: relative;
  left: 0;
}

.links {
  color: black;
  flex: 1 1;
  padding: 15px;
  border: 15px;
  margin: 15px;
  font-family: monaco;
  text-align: center;
}

.links:hover {
  color: white;
  background-color: black;
}

.title {
  text-align: center;
  padding: 20px;
  color: black;
  font-size: 5vh;
  font-family: monaco;
}

.icon {
  opacity: 0.7;
  flex: 1 1;
  padding: 15px;
  border: 15px;
  margin: 15px;
}
.icon:hover {
  opacity: 1;
}

.banner1 {
  display: flex;
  width: 50%;
}

.banner2 {
  display: flex;
  width: 50%;
}
